import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SeccionGeneralPage } from './seccion-general.page';

const routes: Routes = [
  {
    path: '',
    component: SeccionGeneralPage,
  },
  {
    path: 'seccion-multiusos',
    loadChildren: () =>
      import('src/app/pages/SST/informacion-actividad/seccion-multiusos/seccion-multiusos.module').then(m => m.SeccionMultiusosPageModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SeccionGeneralPageRoutingModule {}
