import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertController, ModalController, Platform } from '@ionic/angular';
import { MaestroCuestionario } from 'src/app/interfaces/maestro-cuestionario';
import { DataQuestionsService } from 'src/app/services/SST/data-questions.service';
import { UtilsService } from 'src/app/services/utils.service';
import { ModalTrabajadoresComponent } from 'src/app/components/modal-trabajadores/modal-trabajadores.component';
import { SaveDataService } from 'src/app/services/SST/save-data.service';
import { EDGuardarPermisoTrabajoAPP } from 'src/app/interfaces/edguardar-permiso-trabajo-app';
import { TablaTrabajadoresService } from 'src/app/services/SST/tabla-trabajadores.service';
import { ASTWithName } from '@angular/compiler';

@Component({
  selector: 'app-seccion-general',
  templateUrl: './seccion-general.page.html',
  styleUrls: ['./seccion-general.page.scss'],
})
export class SeccionGeneralPage implements OnInit {
  data: EDGuardarPermisoTrabajoAPP;

  questions: MaestroCuestionario[];
  submitted = false;

  constructor(
    private questionService: DataQuestionsService,
    private utils: UtilsService,
    private alertController: AlertController,
    private router: Router,
    private platform: Platform,
    public modalController: ModalController,
    private dataService: SaveDataService,
    private tablaTrabajadoresService: TablaTrabajadoresService
  ) {
    this.precargarDatos();
  }

  ngOnInit() {}

  verTrabajadores() {
    this.modalController
      .create({
        component: ModalTrabajadoresComponent,
      })
      .then(modal => {
        modal.present();
      });
  }

  private async getQuestions() {
    await this.utils.presentLoading();
    const seccio = await this.questionService.getSeccionGeneral();
    this.questions = seccio;
    this.questions.forEach(question => {
      question.strRespuesta = '';
    });
    this.utils.dismissLoading();
  }

  async checkQuestions(questions: MaestroCuestionario[]): Promise<boolean> {
    for (const question of questions) {
      if (!question.strRespuesta) {
        await this.mostrarError('Debe responder todas las preguntas');
        return false;
      }
    }
    return true;
  }

  continuar() {
    this.submitted = true;
    this.checkQuestions(this.questions).then(async result => {
      if (result) {
        await this.guardarDatos();
        this.router.navigate(['/home/permisos/registro-permiso/crear-permiso/inicio-permiso/seccion-general/seccion-multiusos']);
      }
    });
  }

  private async mostrarError(message: string) {
    const alert = await this.alertController.create({
      header: 'Alerta',
      message: message,
      buttons: ['OK'],
    });
    alert.present();
  }

  async guardarDatos() {
    console.log('guardarDatos');
    if (this.data['listaMaestroCuestionario'].length === 0) {
      this.data['listaMaestroCuestionario'] = this.questions;
    } else {
      let filtro = this.data['listaMaestroCuestionario'].filter(item => item.strTitulo !== 'SECCIÓN GENERAL');
      this.data['listaMaestroCuestionario'] = filtro.concat(this.questions);
    }
    this.dataService.setData(this.data);
  }

  precargarDatos() {
    const dataUrl = history.state.data;
    console.log(dataUrl);
    if (dataUrl) {
      this.dataService.clearData();
      this.tablaTrabajadoresService.setListaTrabajadores(dataUrl['listaTrabajadoresMigrados']);
      this.dataService.setData(dataUrl);
      this.data = dataUrl;
      this.questions = dataUrl['listaMaestroCuestionario'].filter(item => item.strTitulo === 'SECCIÓN GENERAL');
    } else {
      this.data = this.dataService.getData();
      let isQuestion = this.data['listaMaestroCuestionario'].filter(item => item.strTitulo === 'SECCIÓN GENERAL');
      if (isQuestion.length > 0) {
        this.questions = isQuestion;
      } else {
        this.getQuestions();
      }
    }
  }
  back() {
    this.router.navigate(['/home/permisos/registro-permiso/crear-permiso/inicio-permiso'], { state: { data: this.data } });
  }
}
