  /* Descripción de la Actividad */
  getDescripcionActividad(): Observable<DescripcionActividad[]> {
    return this.http.get<{ desc_actividad: DescripcionActividad[] }>('/assets/data/questionSST.json').pipe(
      map(data => {
        if (Array.isArray(data.desc_actividad)) {
          return data.desc_actividad;
        } else return [];
      })
    );
  }

